import util from '@/utils/util.js';	
export default {
	components:{

	},		
	data() {
		return {
			
		}
	},
	props:{
		isCard:{
			type: Boolean,
			default: false
		},
		listData:{
			type: Array,
			default: []
		},				
	},		
	methods: {
		toPeople:function(e){
			console.log('toPeople',e)
			var uid = e.currentTarget.dataset.uid;
			if(uid > 0){
				uni.navigateTo({
					url:'../people/people?uid=' + uid
				})
			}else{
				var anonymous = e.currentTarget.dataset.anonymous
				if(anonymous != 1){
					util.showToast('会员ID有误')
				}
			}		
		},			
		toQuestionDetail:function(e){
			var id = e.currentTarget.dataset.id;
			if(id > 0){
				uni.navigateTo({
					url:'../question/detail?id=' + id
				})
			}else{
				util.showToast('id有误')
			}
		},	
		toArticleDetails:function(e){
			var id = e.currentTarget.dataset.id;
			if(id > 0){
				uni.navigateTo({
					url:'../article/detail?id=' + id
				})
			}else{
				util.showToast('有误')
			}
		},			
	}
}
